import { useState, useCallback } from 'react'
import Card from 'react-bootstrap/Card'
import Tab from 'react-bootstrap/Tab'
import Tabs from 'react-bootstrap/Tabs'
import Button from 'react-bootstrap/Button'
import Modal from 'react-bootstrap/Modal'
import Form from 'react-bootstrap/Form'
import InputGroup from 'react-bootstrap/InputGroup'
import { useObservable } from 'rxjs-hooks'
import md5 from 'md5'
import { plugins$, PluginProps, selectedPlugin$ } from 'states/preferences'

function Plugin({ id, name, createdAt, updatedAt }: PluginProps) {
  const selected = useObservable(() => selectedPlugin$, null)
  const handleClick = useCallback(() => {
    selectedPlugin$.next(id)
  }, [])
  return (
    <Card
      css={`
        background-color: ${selected === id ? '#f5f5f5' : '#fff'};
      `}
      onClick={handleClick}>
      <Card.Body>
        <Card.Title>{name}</Card.Title>
        <Card.Text>
          <span>created at {createdAt.toLocaleDateString()}</span>
          {updatedAt ? <span>last updated at {updatedAt.toLocaleDateString()}</span> : null}
        </Card.Text>
      </Card.Body>
    </Card>
  )
}

export function Plugins() {
  const plugins = useObservable(() => plugins$, [])
  const [show, setShow] = useState<boolean>(false)
  const handleCreate = useCallback(() => setShow(true), [])
  const handleClose = useCallback(() => setShow(false), [])
  const [plugin, setPlugin] = useState<Partial<PluginProps>>({ name: '', url: '' })
  const handleOk = useCallback(() => {
    if (!(plugin.name && plugin.url)) {
      return
    }
    const p = { ...plugin, id: md5(plugin.url), createdAt: new Date() } as PluginProps
    if (plugin) {
      plugins$.next([...plugins$.value, p])
    }
    setPlugin({ name: '', url: '' })
    setShow(false)
  }, [plugin])
  return (
    <div>
      <div>
        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Create you own plugin</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <InputGroup className="mb-3">
              <InputGroup.Text id="basic-addon1">Name</InputGroup.Text>
              <Form.Control
                placeholder="Input a meaningful name"
                aria-label="plugin-name"
                aria-describedby="name of this plugin"
                name="name"
                value={plugin?.name}
                onChange={(e) => setPlugin((p) => ({ ...p, name: e.target.value }))}
              />
            </InputGroup>
            <InputGroup>
              <InputGroup.Text>URL</InputGroup.Text>
              <Form.Control
                as="textarea"
                aria-label="plugin-url"
                name="url"
                value={plugin?.url}
                onChange={(e) => setPlugin((p) => ({ ...p, url: e.target.value }))}
              />
            </InputGroup>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button variant="primary" onClick={handleOk}>
              Save
            </Button>
          </Modal.Footer>
        </Modal>
        <Button onClick={handleCreate}>Create</Button>
      </div>
      {plugins.map((plugin) => (
        <Plugin key={plugin.id} {...plugin} />
      ))}
    </div>
  )
}

export default function Preferences() {
  return (
    <Tabs defaultActiveKey="plugins" id="uncontrolled-tab-example" className="mb-3">
      <Tab eventKey="general" title="General">
        General information
      </Tab>
      <Tab eventKey="plugins" title="Plugins">
        <Plugins />
      </Tab>
    </Tabs>
  )
}
