import { BehaviorSubject, combineLatest, interval } from 'rxjs'
import { distinctUntilChanged, map, shareReplay, tap } from 'rxjs/operators'

export interface PluginProps {
  readonly id: string
  name: string
  url: string
  readonly createdAt: Date
  updatedAt?: Date
}

export type SelectedPlugin = string

export const plugins$ = new BehaviorSubject<Array<PluginProps>>([])
export const selectedPlugin$ = new BehaviorSubject<string | null>(null)

export const preferences$ = combineLatest([plugins$, selectedPlugin$]).pipe(
  map(([plugins, selectedPlugin]) => ({ plugins, selectedPlugin })),
  distinctUntilChanged(),
  shareReplay(2)
)
